.dialog-close {
  position: absolute;
  z-index: 1;
  right: 20px;
  top: 10px;
  cursor: pointer;
}

.dialog-container {
  float: none;
  max-width: 1300px;
  margin: 20px;

  .header {
    font-size: 20px;
    font-weight: bold;
    margin: 15px auto;
    text-align: center;
  }
  .content {
    margin-bottom: 10px;
  }
  .footer {
    width: 28%;
  }
}



